.menu {
  width: 100%;
  min-height: 446px;
  background-color: white;
  box-shadow: 0px 4px 20px 0px rgba(0, 7, 94, 0.102);
  position: absolute;
  top: 0;
  z-index: 5;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 0 10px 40px 10px;
  opacity: 0;
  display: none;
  transition: display 0.2s ease-in-out;
}
.menu.active {
  opacity: 100%;
  display: block;
}
.menu__input {
  width: 100%;
  height: 100%;
  border-radius: 50px;
  outline: 0;
  border: 0;
  background-color: #F0F5FB;
  padding-right: 40px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 19px;
  font-size: 14px;
  color: var(--gray-text-color);
}
.menu__input::placeholder {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 19px;
  font-size: 14px;
  color: rgba(24, 21, 75, 0.3);
}
.menu__input-wrapper {
  margin-top: 20px;
  position: relative;
  width: 300px;
  min-height: 40px;
  border-radius: 50px;
  outline: 0;
  border: 0;
  background-color: #F0F5FB;
  display: flex;
  align-items: center;
  padding-left: 15px;
}
.menu__button-input {
  position: absolute;
  right: 15px;
  width: 18px;
  height: 18px;
  background-color: #F0F5FB;
  border: none;
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_10___});
}
.menu__list {
  list-style: none;
  width: 100%;
  padding: 0;
}
.menu__list--hide {
  max-height: 0;
  overflow: hidden;
  transition: max-height 0.5s ease-in-out;
}
.menu__list--hide.active {
  max-height: 200px;
  width: 100%;
}
.menu a, .menu a:visited, .menu a:active {
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 22px;
  font-size: 16px;
  color: #303239;
  text-decoration: none;
}
.menu__item {
  border-bottom: 1px solid rgb(240, 245, 251);
  height: 42px;
  width: 100%;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 22px;
  font-size: 16px;
  color: #303239;
  padding: 10px;
}
.menu__item--hide {
  opacity: 0;
  width: 100%;
  transition: opacity 0.5s ease-in-out;
  padding: 10px 10px 10px 20px;
  border-bottom: 1px solid rgb(240, 245, 251);
}
.menu__item--hide.active {
  opacity: 100%;
  width: 100%;
}
.menu .dropdown {
  position: relative;
  cursor: pointer;
  display: flex;
  justify-content: space-between;
  width: 100%;
}
.menu .dropdown a {
  flex: 2 0 50%;
}
.menu__button {
  padding: 0;
  margin: 0;
  height: 11px;
  width: 11px;
  border-bottom: 1px;
  border-right: 1px;
  background-color: transparent;
  transform: rotate(-135deg);
  border-radius: 1px;
  transition: transform 0.5s ease-in-out;
}
.menu__button.active {
  transform: rotate(45deg);
}
.menu__info {
  margin-top: 30px;
  width: 100%;
  display: flex;
  flex-direction: column;
  gap: 15px;
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 19px;
  font-size: 14px;
  color: var(--gray-text-color);
}
.menu__info span {
  width: 100%;
  padding-left: 36px;
  position: relative;
}
.menu__email::before, .menu__phone::before {
  content: "";
  position: absolute;
  left: 10px;
  top: 50%;
  transform: translateY(-50%);
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  width: 16px;
  height: 16px;
}
.menu__email::before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_11___});
}
.menu__phone::before {
  background-image: url(${___CSS_LOADER_URL_REPLACEMENT_12___});
}
.menu__footer {
  height: 40px;
  width: 300px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-top: 15px;
}
.menu__footer-button {
  width: 160px;
  height: 100%;
  border-radius: 50px;
  border: none;
  background: linear-gradient(73.56deg, #0D748B -10.13%, #1A0B73 75.49%);
  font-family: "Nunito Sans", sans-serif;
  font-weight: 700;
  line-height: 18px;
  font-size: 13px;
  color: var(--bg-color-white);
}
.menu__socials {
  width: 70px;
  height: 100%;
  display: flex;
  gap: 10px;
  align-items: center;
}
.menu__icon {
  width: 30px;
  height: 30px;
}

@media (min-width: 1281px) {
  .menu {
    display: none;
  }
}
@media (min-width: 1920px) {
  .menu {
    display: none;
  }
}
