input[type=tel]:not(.menu__input):valid {
  border: 1px solid rgba(243, 243, 243, 0.7);
  background-color: rgba(255, 255, 255, 0.3);
  font-family: "Nunito Sans", sans-serif;
  font-weight: 400;
  line-height: 19px;
  font-size: 14px;
  color: rgb(243, 243, 243);
}

